import React from 'react'
import { Route, Redirect } from 'react-router-dom'

import { EntryListContainer, JudgingPanelContainer, ReviewScoringContainer, FinalizedSummaryContainer } from '../pages/containers'

function validateUserFinalization (hasFinalized, route, componentToRender) {
    return (true === hasFinalized) ? 
        <Route path={route} component={() => <Redirect to="/judge-app/overall-judging-summary" />} />
        :
        <Route path={route} component={ componentToRender } />
}

const MainBody = ({stateTree}) => {
    
    function validateUserFinalization (hasFinalized, route, componentToRender) {
        return (true === hasFinalized) ? 
            <Route path={route} component={() => <Redirect to="/judge-app/overall-judging-summary" />} />
            :
            <Route path={route} component={ componentToRender } />
    }
    
    return (
    <div id="mainbody-container" className="max-height">
        <div id="contents-section">
            {
                validateUserFinalization(stateTree.userDetails.userHasFinalized, "/judge-app/entry-list", EntryListContainer)
            }
            {
                validateUserFinalization(stateTree.userDetails.userHasFinalized, "/judge-app/judging-panel/:entryId", JudgingPanelContainer)
            }
            {
                validateUserFinalization(stateTree.userDetails.userHasFinalized, "/judge-app/review-scoring", ReviewScoringContainer)
            }
            <Route path="/judge-app/overall-judging-summary" component={ FinalizedSummaryContainer } />
        </div>
    </div>
    )
}

export default MainBody;


